  height: 50px;
  background: white;
  border-radius: 5px;
  opacity: 0.8; }

.none {
  display: none; }

.ready1 {
  height: 30%; }

.ready2 {
  height: 58%; }

.ready3 {
  height: 85%; }

.button-enter {
  opacity: 0.01;
  -webkit-transform: translate(100px);
      -ms-transform: translate(100px);
          transform: translate(100px); }

